"use client";

import React from "react";
import { Skeleton, SkeletonCard, SkeletonAvatar, SkeletonText } from "../ui/skeleton";

export const ProjectCardSkeleton = () => {
  return (
    <div className="mb-3 p-4 bg-card rounded-lg border border-border/50 shadow-soft">
      <div className="space-y-3">
        {/* Header */}
        <div className="flex items-start justify-between">
          <div className="flex-1">
            <Skeleton className="h-4 w-3/4" />
          </div>
          <Skeleton className="h-6 w-6 rounded" />
        </div>

        {/* Description */}
        <SkeletonText lines={2} />

        {/* Priority and Due Date */}
        <div className="flex items-center justify-between">
          <Skeleton className="h-6 w-16 rounded-full" />
          <Skeleton className="h-4 w-20" />
        </div>

        {/* Project Info */}
        <div className="flex items-center gap-2 pb-2 border-b border-border/50">
          <SkeletonAvatar className="!h-5 !w-5" />
          <Skeleton className="h-3 w-24" />
        </div>

        {/* Assignee */}
        <div className="flex items-center justify-between">
          <div className="flex items-center gap-2">
            <SkeletonAvatar className="!h-6 !w-6" />
            <Skeleton className="h-3 w-20" />
          </div>
          <Skeleton className="h-3 w-12" />
        </div>
      </div>
    </div>
  );
};

export const ProjectKanbanSkeleton = () => {
  return (
    <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
      {["To Do", "In Progress", "Done"].map((status, index) => (
        <div key={index} className="space-y-4">
          <div className="flex items-center justify-between">
            <Skeleton className="h-6 w-20" />
            <Skeleton className="h-6 w-8 rounded-full" />
          </div>
          <div className="space-y-3">
            {[...Array(3)].map((_, cardIndex) => (
              <ProjectCardSkeleton key={cardIndex} />
            ))}
          </div>
        </div>
      ))}
    </div>
  );
};

export const WorkspaceListSkeleton = () => {
  return (
    <div className="space-y-4">
      {[...Array(4)].map((_, index) => (
        <div key={index} className="p-4 border border-border rounded-lg bg-card">
          <div className="flex items-start gap-4">
            <SkeletonAvatar className="!h-12 !w-12" />
            <div className="flex-1 space-y-2">
              <Skeleton className="h-5 w-48" />
              <SkeletonText lines={2} />
              <div className="flex items-center gap-4 mt-3">
                <Skeleton className="h-4 w-20" />
                <Skeleton className="h-4 w-24" />
                <Skeleton className="h-4 w-16" />
              </div>
            </div>
            <Skeleton className="h-8 w-8" />
          </div>
        </div>
      ))}
    </div>
  );
};

export const WorkspaceDashboardSkeleton = () => {
  return (
    <div className="p-6 space-y-6 animate-fade-in">
      {/* Header Section */}
      <div className="space-y-2">
        <Skeleton className="h-8 w-64" />
        <Skeleton className="h-4 w-96" />
      </div>

      {/* Stats Cards */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
        {[...Array(4)].map((_, index) => (
          <div key={index} className="p-4 border border-border rounded-lg bg-card space-y-3">
            <div className="flex items-center justify-between">
              <Skeleton className="h-4 w-16" />
              <Skeleton className="h-4 w-4" />
            </div>
            <Skeleton className="h-8 w-20" />
            <Skeleton className="h-3 w-24" />
          </div>
        ))}
      </div>

      {/* Recent Activity & Quick Actions */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {/* Recent Projects */}
        <div className="space-y-4">
          <div className="flex items-center justify-between">
            <Skeleton className="h-6 w-32" />
            <Skeleton className="h-8 w-20" />
          </div>
          <div className="space-y-3">
            {[...Array(3)].map((_, index) => (
              <div key={index} className="p-3 border border-border rounded-lg bg-card">
                <div className="flex items-start gap-3">
                  <SkeletonAvatar className="!h-10 !w-10" />
                  <div className="flex-1 space-y-2">
                    <Skeleton className="h-4 w-32" />
                    <SkeletonText lines={2} />
                    <div className="flex items-center gap-2">
                      <Skeleton className="h-5 w-16 rounded-full" />
                      <Skeleton className="h-4 w-20" />
                    </div>
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>

        {/* Recent Tasks */}
        <div className="space-y-4">
          <div className="flex items-center justify-between">
            <Skeleton className="h-6 w-28" />
            <Skeleton className="h-8 w-24" />
          </div>
          <div className="space-y-3">
            {[...Array(4)].map((_, index) => (
              <div key={index} className="p-3 border border-border rounded-lg bg-card">
                <div className="flex items-center justify-between">
                  <div className="flex items-center gap-3 flex-1">
                    <Skeleton className="h-4 w-4" />
                    <div className="space-y-1 flex-1">
                      <Skeleton className="h-4 w-48" />
                      <Skeleton className="h-3 w-32" />
                    </div>
                  </div>
                  <div className="flex items-center gap-2">
                    <Skeleton className="h-5 w-12 rounded-full" />
                    <SkeletonAvatar className="!h-6 !w-6" />
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
};

export const MyTasksSkeleton = () => {
  return (
    <div className="p-6 space-y-6">
      {/* Header */}
      <div className="space-y-2">
        <Skeleton className="h-8 w-40" />
        <Skeleton className="h-4 w-72" />
      </div>

      {/* Filters */}
      <div className="flex items-center gap-4">
        <Skeleton className="h-10 w-48" />
        <Skeleton className="h-10 w-32" />
        <Skeleton className="h-10 w-28" />
      </div>

      {/* Task Table */}
      <div className="border border-border rounded-lg bg-card">
        {/* Table Header */}
        <div className="p-4 border-b border-border">
          <div className="grid grid-cols-12 gap-4">
            <Skeleton className="h-4 w-8 col-span-1" />
            <Skeleton className="h-4 w-16 col-span-3" />
            <Skeleton className="h-4 w-12 col-span-2" />
            <Skeleton className="h-4 w-14 col-span-2" />
            <Skeleton className="h-4 w-16 col-span-2" />
            <Skeleton className="h-4 w-12 col-span-2" />
          </div>
        </div>
        
        {/* Table Rows */}
        <div className="divide-y divide-border">
          {[...Array(8)].map((_, index) => (
            <div key={index} className="p-4">
              <div className="grid grid-cols-12 gap-4 items-center">
                <Skeleton className="h-4 w-4 col-span-1" />
                <div className="col-span-3 space-y-1">
                  <Skeleton className="h-4 w-32" />
                  <Skeleton className="h-3 w-24" />
                </div>
                <Skeleton className="h-5 w-16 rounded-full col-span-2" />
                <Skeleton className="h-5 w-20 rounded-full col-span-2" />
                <div className="col-span-2 flex items-center gap-2">
                  <SkeletonAvatar className="!h-6 !w-6" />
                  <Skeleton className="h-3 w-16" />
                </div>
                <Skeleton className="h-4 w-16 col-span-2" />
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export const WorkspaceSettingsSkeleton = () => {
  return (
    <div className="p-6 space-y-8">
      {/* Header */}
      <div className="space-y-2">
        <Skeleton className="h-8 w-48" />
        <Skeleton className="h-4 w-80" />
      </div>

      {/* Workspace Info */}
      <div className="space-y-6">
        <div className="space-y-4">
          <Skeleton className="h-6 w-40" />
          <div className="space-y-4 max-w-md">
            <div className="space-y-2">
              <Skeleton className="h-4 w-24" />
              <Skeleton className="h-10 w-full" />
            </div>
            <div className="space-y-2">
              <Skeleton className="h-4 w-20" />
              <Skeleton className="h-24 w-full" />
            </div>
          </div>
        </div>

        {/* Members Section */}
        <div className="space-y-4">
          <div className="flex items-center justify-between">
            <Skeleton className="h-6 w-32" />
            <Skeleton className="h-8 w-28" />
          </div>
          <div className="space-y-3">
            {[...Array(4)].map((_, index) => (
              <div key={index} className="flex items-center justify-between p-3 border border-border rounded-lg">
                <div className="flex items-center gap-3">
                  <SkeletonAvatar className="!h-10 !w-10" />
                  <div className="space-y-1">
                    <Skeleton className="h-4 w-32" />
                    <Skeleton className="h-3 w-48" />
                  </div>
                </div>
                <div className="flex items-center gap-2">
                  <Skeleton className="h-6 w-16 rounded-full" />
                  <Skeleton className="h-8 w-8" />
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
};